@import url("https://fonts.googleapis.com/css2?family=Signika+Negative:wght@500&display=swap");
.header {
  background-color: #4696e5;
  color: #fafafa;
  padding: 0.5rem;
  border-radius: 15px 15px 3px 3px;
  text-align: center;
}
.header h1 {
  font-size: 2rem;
  font-weight: bold;
}

.form-container {
  padding: 5px;
}
.form-container input {
  font-weight: bold;
  font-size: 1rem;
  text-transform: capitalize;
  margin: 2px;
  padding: 10px;
  width: 75%;
  border: 2px solid #4696e5;
  border-radius: 10px;
  margin-right: 10px;
  color: #444;
}
.form-container input::-moz-placeholder {
  color: #666;
}
.form-container input:-ms-input-placeholder {
  color: #666;
}
.form-container input::placeholder {
  color: #666;
}
.form-container button {
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 8px;
  border: none;
  border-radius: 10px;
  width: 20%;
  background-color: #4696e5;
  color: #fafafa;
}

.list-container {
  font-family: "Tajawal", sans-serif;
  width: calc(100% - 6px);
  text-transform: capitalize;
  padding: 5px;
}
.list-container li {
  background-color: #fafafa;
  border: 2px solid #21212121;
  border-radius: 10px;
  padding: 7px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.list-container li button {
  cursor: pointer;
  padding: 5px;
  background-color: #dc3545;
  color: #fafafa;
  border: none;
  border-radius: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Signika Negative", sans-serif;
  background-color: #d9d8ff;
  display: flex;
  justify-content: center;
}

.App {
  background-color: #f5f5f5f5;
  margin: 2rem;
  min-height: 25rem;
  min-width: 25rem;
  border-radius: 15px;
  box-shadow: rgba(21, 21, 21, 0.5) 0 5px 10px;
}

@media (max-width: 500px) {
  .App {
    min-width: 20rem;
  }

  .form-container input {
    font-size: 0.8rem;
  }
  .form-container button {
    font-size: 1.1rem;
  }
}
@media (max-width: 355px) {
  .header h1 {
    font-size: 1.6rem;
  }
}/*# sourceMappingURL=main.css.map */