.list-container {
  font-family: "Tajawal", sans-serif;
  width: calc(100% - 6px);
  text-transform: capitalize;
  padding: 5px;
  li {
    background-color: #fafafa;
    border: 2px solid #21212121;
    border-radius: 10px;
    padding: 7px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    button {
      cursor: pointer;
      padding: 5px;
      background-color: #dc3545;
      color: #fafafa;
      border: none;
      border-radius: 10px;
    }
  }
}
