.form-container {
  padding: 5px;
  input {
    font-weight: bold;
    font-size: 1rem;
    text-transform: capitalize;
    margin: 2px;
    padding: 10px;
    width: 75%;
    border: 2px solid #4696e5;
    border-radius: 10px;
    margin-right: 10px;
    color: #444;
    &::placeholder {
      color: #666;
    }
  }
  button {
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 8px;
    border: none;
    border-radius: 10px;
    width: 20%;
    background-color: #4696e5;
    color: #fafafa;
  }
}
